import React, { useState, useEffect, useRef } from "react";
import { useParams } from "react-router-dom";
import { SwiperSlide } from "swiper/react";
import { useHistory } from "react-router";
import Section from "../../components/Section";
import Card from "../../components/Card";
import HorizontalList from "../../components/HorizontalList";
import * as S from "./styles";
import Layout from "../../layout";
import Share from "../../components/CardCompartilhar/index";
import Horarios from "../Horarios";
import { useShows } from "../../dados/context/ShowsContext";
import { useCarts } from "../../dados/context/CartContext";
import app from "../../auth/config";
import ButtonContinuar from "../../components/ButtonContinuar";
import { useAuth } from "../../auth/auth";
import SpectaclesCarousel from "../../components/SpectaclesCarousel";

function Cardezao() {
  const { show: espetaculo, emAltaList, showLike, getShowWithLink, getShowById, adquiridos, getShowsByUser, isInFavorites } = useShows();
  const elementRef = useRef();
  const { signature, userIsAuthenticated } = useAuth();
  const userId = userIsAuthenticated() ? app.auth().currentUser.uid : "";
  const [abriu, setAbri] = useState(false);
  const [abrirCard, setAbrirCardzao] = useState(false);
  const history = useHistory();
  const [compartilharFica, setcompartilharFicha] = useState(false);
  const [compartilhar, setcompartilhar] = useState(false);
  const { id } = useParams();
  const hasSpectacle = userIsAuthenticated()? adquiridos.find(show=>show.id===id): false;

  const [eh3d, setEh3d] = useState(false);
  const [ehVr, setEhVr] = useState(false);
  const [checked, setChecked] = useState(false);

  // console.log(history);
  useEffect(() => {
    id && getShowById(id);
  }, [id]);

  function fechar() {
    setAbri(false);
  }
  useEffect(() => {
    userIsAuthenticated() && getShowsByUser()
  }, []);

  function handleStyle () {
    const glow = document.getElementById('glow')
    glow.style.color = 'red' 
    glow.style.fontWeight = 'bold'
  }

  const cart = useCarts();

  function handle3dToCart() {
    setEh3d(false) 
    
    if (!userIsAuthenticated()) {
      cart.addToCart({
        id: espetaculo.id,
        valorPreco: espetaculo.price_on_demand,
        link: espetaculo.link,
        direcao: espetaculo.direction,
        producao: espetaculo.production,
        elenco: espetaculo.cast,
        artista: espetaculo.producer_id,
        sinopsiaEsp: espetaculo.synopsis,
        descricaoClassifica: espetaculo.ageRatingDescription,
        classificacao: espetaculo.ageRating,
        thumbnail: espetaculo.thumbnail,
        tituloEsp: espetaculo.title,
        artistaEsp: espetaculo.producer_id,
        categoria: espetaculo.producer_id,
        videoID: espetaculo.videoID,
        videoID2: espetaculo.videoID2,
        trailher: espetaculo.trailer,
      });
      history.push(`/cart`);
      }  else if (userIsAuthenticated()) {
        getShowsByUser();
        //fazer um find e verificar na rota de filmes adquiridos se esse espetáculo está lá
        const hasSpectacle = adquiridos.find(
          (show) => show.id === espetaculo.id
        );
        if (hasSpectacle) {
          // pegar parametros da rota privada
          getShowWithLink(espetaculo.id, (error, response) => {
            response &&
              history.push(
                `/playespetaculo/${response.videoID}/${response.videoID2}/${response.id}`
              );
          });
        } else {
          cart.addToCart({
            id: espetaculo.id,
            valorPreco: espetaculo.price_on_demand,
            link: espetaculo.link,
            direcao: espetaculo.direction,
            producao: espetaculo.production,
            elenco: espetaculo.cast,
            artista: espetaculo.producer_id,
            sinopsiaEsp: espetaculo.synopsis,
            descricaoClassifica: espetaculo.ageRatingDescription,
            classificacao: espetaculo.ageRating,
            thumbnail: espetaculo.thumbnail,
            tituloEsp: espetaculo.title,
            artistaEsp: espetaculo.producer_id,
            categoria: espetaculo.producer_id,
            videoID: espetaculo.videoID,
            videoID2: espetaculo.videoID2,
            trailher: espetaculo.trailer,
          });
          history.push(`/cart`);
        }
      } else {
      history.push(
        `/playcanalpw/${espetaculo.videoID}/${espetaculo.videoID2}`
      );
    }
  }

  return (
    <Layout>
      {eh3d && (
        <Layout>
        <S.Container>
          <S.PopapDiv style={{ height: "100%" }}>
            <S.PopapDiv1 style={{ height: "100%", padding: "20px" }}>

              <S.Span style={{fontWeight: 'bold'}} cor="black" size="1.8" className="mb-2 mt-2">
                Espetáculo 3D
              </S.Span>

              <S.Span cor="black" size="1.8" className="mt-2 mb-5">
                Este é um espetáculo 3D e para assistir é necessário uso de óculos VR.  <br/><br/>Uso exclusivo para as peças em visualização VR do PalcoWeb.  <br/><br/>Ideal para smartphones de 4.7'' a 6.5''. O uso desse equipamento com óculos de grau depende da armação. Não nos responsabilizamos pelo uso inadequado do produto.<br/>
                
                <form style={{textAlign:'center', justifyContent:'space-evenly'}} onClick={() => setChecked(!checked)}>
                  <input style={{width:'30%'}} type="checkbox" />
                  <label>Li e aceito.</label>
                </form>

                {!checked && <div id='glow'>Aceite os termos antes de prosseguir.</div>}
              </S.Span>

              <S.PopapDivButton>
                <S.PopapButton onClick={(e) => checked ? handle3dToCart() : handleStyle() }>
                  <S.Span size="1.5">Continuar</S.Span>
                </S.PopapButton>
              </S.PopapDivButton>

            </S.PopapDiv1>
          </S.PopapDiv>
        </S.Container>
      </Layout>
      )}

      {ehVr && (
        <Layout>
        <S.Container>
          <S.PopapDiv style={{ height: "100%" }}>
            <S.PopapDiv1 style={{ height: "100%", padding: "20px" }}>

              <S.Span style={{fontWeight: 'bold'}} cor="black" size="1.8" className="mb-2 mt-2">
                Comprar óculos VR
              </S.Span>

              <S.Span cor="black" size="1.8" className="mt-2 mb-5">
                Acesse o link do nosso parceiro e compre o seu por R$49,90 + frete. <br/><br/>Uso exclusivo para as peças em visualização VR do PalcoWeb.  <br/><br/>Ideal para smartphones de 4.7'' a 6.5''. O uso desse equipamento com óculos de grau depende da armação. Não nos responsabilizamos pelo uso inadequado do produto.<br/>
                
                <form style={{textAlign:'center', justifyContent:'space-evenly'}} onClick={() => setChecked(!checked)}>
                  <input style={{width:'30%'}} type="checkbox" />
                  <label>Li e aceito.</label>
                </form>

                {!checked && <div id='glow'>Aceite os termos antes de prosseguir.</div>}
              </S.Span>

              <S.PopapDivButton>
                <S.PopapButton style={{background:'transparent'}} onClick={() => setEhVr(false)}>
                  <S.Span style={{color:'black'}} size="1.5">Voltar</S.Span>
                </S.PopapButton>

                <S.PopapButton onClick={() =>  checked ? window.open('https://produto.mercadolivre.com.br/MLB-1814036328-oculos-realidade-virtual-3d-compativel-com-todos-smartphones-_JM') : handleStyle()}>
                  <S.Span size="1.5">Comprar</S.Span>
                </S.PopapButton>
              </S.PopapDivButton>

            </S.PopapDiv1>
          </S.PopapDiv>
        </S.Container>
      </Layout>
      )}

      {!eh3d && !ehVr && abriu ? (
        <Horarios
          aidKey={espetaculo.id}
          direcao={espetaculo.direction}
          producao={espetaculo.production}
          elenco={espetaculo.cast}
          voltar={fechar}
          sinopsiaEsp={espetaculo.synopsis}
          descricaoClassifica={espetaculo.ageRatingDescription}
          classificacao={espetaculo.ageRating}
          img={espetaculo.thumbnail}
          tituloEsp={espetaculo.title}
          artistaEsp={espetaculo.producer_id}
          categoria={espetaculo.producer_id}
          videoID={espetaculo.videoID}
          videoID2={espetaculo.videoID2}
        ></Horarios>
      ) : (
        <>
          {!eh3d && !ehVr && abrirCard ? (
            <></>
          ) : !eh3d && !ehVr && (
            <>
              <S.Container ref={elementRef}>
                <S.ContainerImg>
                  {/* {console.log(espetaculo?.trailer)} */}
                  {espetaculo?.trailer ? (
                    <iframe
                      src={espetaculo?.trailer}
                      width="1920"
                      height="1080"
                      frameBorder="0"
                      allow="autoplay; fullscreen; picture-in-picture"
                      allowfullscreen
                      title="Cad&amp;ecirc; (COMPLETO)"
                    ></iframe>
                  ) : (
                    <S.Span>Sem trailer</S.Span>
                  )}
                </S.ContainerImg>

                <S.Conteudo>
                  <S.ButtonVoltar
                    onClick={() =>
                      history.push("/") || window.location.reload()
                    }
                  >
                    {" "}
                    <i className="bi bi-x-lg"></i>{" "}
                  </S.ButtonVoltar>
                  {espetaculo?.title?.includes("3d") && <S.Spanzito style={{fontSize:'1rem', background: 'black', padding:'10px', width: '100%', textAlign:'center'}}>OBRIGATÓRIO USO DE ÓCULOS VR</S.Spanzito>}
                  <S.HeaderConteudo>
                    <S.Item>
                      <S.Span>
                        <S.Classificacao>
                          {espetaculo.ageRating}
                        </S.Classificacao>{" "}
                        <S.Span className="m-2">
                          {espetaculo.ageRatingDescription}
                        </S.Span>
                      </S.Span>
                    </S.Item>

                    <S.Item>
                      {userIsAuthenticated() && (
                        <S.Coracao
                          
                          onClick={() => {
                            showLike(userId, espetaculo.id);
                          }}
                        >
                          <i className="bi bi-suit-heart-fill"></i>
                        </S.Coracao>
                      )}

                      <S.Compartilhar>
                        {" "}
                        <i
                          className="bi bi-share-fill"
                          onMouseEnter={() => setcompartilharFicha(true)}
                        ></i>
                        {compartilharFica && (
                          <Share
                            link={`http://www.palcoweb.com.br/cardezao/${espetaculo.id}`}
                            sairficha={setcompartilharFicha}
                          ></Share>
                        )}
                      </S.Compartilhar>
                    </S.Item>
                  </S.HeaderConteudo>

                  <S.Informacoes>
                    <S.Item>
                      <S.HeaderTitle> {espetaculo.title}</S.HeaderTitle>
                    </S.Item>
                    <S.Item>
                      <S.Span> Com {espetaculo.cast}</S.Span>
                    </S.Item>
                    <S.Item></S.Item>
                    <S.Item>
                      <S.Text> {espetaculo.synopsis}</S.Text>
                    </S.Item>
                    <S.Item>
                      <S.Span> Elenco: {espetaculo.cast}</S.Span>
                    </S.Item>
                    <S.Item>
                      <S.Span> producao: {espetaculo.production}</S.Span>
                    </S.Item>
                    <S.Item>
                      <S.Span> direcao: {espetaculo.direction}</S.Span>
                    </S.Item>
                    <S.Item>
                      <S.Span> duração: {espetaculo.duration}</S.Span>
                    </S.Item>
                    {!espetaculo.isActiveSignature && (
                      <S.Item>
                        <S.Spanzito> R{espetaculo.price_on_demand?.replace(".",",")}</S.Spanzito>
                      </S.Item>
                    )}

                    <S.Item>
                      {espetaculo?.title?.includes("3d") &&
                        <S.PopapButton style={{border: '1px solid gold', borderRadius:'6px', background:'white'}} onClick={() => {setEhVr(true); document.getElementById('root').scrollTo(0, { behavior: "smooth" })}}>
                          <S.Span style={{color:'black', fontWeight: 'bold'}} size="1.5">Não tenho óculos VR</S.Span>
                        </S.PopapButton>
                      }
                      
                      <ButtonContinuar
                        title={!hasSpectacle? 'COMPRAR':'ASSISTIR'}
                        spectacle={espetaculo}
                        toPlan={
                          espetaculo.isActiveOnDemand ? "onDemand" : "signature"
                        }
                        userIsSignature={
                          userIsAuthenticated() && signature?.isActive
                            ? true
                            : false
                        }
                        userIsAuth={
                          userIsAuthenticated()
                            ? true
                            : false
                        }
                        showModal3d={eh3d} 
                        setShowModal3d={setEh3d}
                      />
                    </S.Item>
                  </S.Informacoes>
                </S.Conteudo>
              </S.Container>
              <Section title="Em Alta" filter>
                  {emAltaList?.map((show) => (
                    <>
                        {show.spectacle &&
                          show.spectacle.length > 0 &&
                          <SpectaclesCarousel
                            spectacles={show.spectacle}
                          >
                          </SpectaclesCarousel>
                        }
                    </>
                  ))}
              </Section>
            </>
          )}
        </>
      )}
    </Layout>
  );
}

export default Cardezao;
